.div-btns{
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;
    gap: 1rem;
}

.generate{
    width: 150px;
    padding: 1rem;
    font-size: 1.5rem;
    font-family: "Poppins";
    border-radius: 5px;
    border: 2px solid rgb(101, 151, 243);
    background: rgb(78, 140, 255);
    color: white;
    box-shadow: 0 7px 0 0 rgb(37, 99, 216);
    transition: .1s ease-in-out;
    z-index: 1;
}
.generate:hover{
    background: rgb(43, 116, 250);
}
.generate:active{
    transform: translateY(4px);
    box-shadow: 0 2px 0 0 rgb(21, 82, 197);
}

.reset{
    width: 150px;
    padding: 1rem;
    font-size: 1.5rem;
    font-family: "Poppins";
    border-radius: 5px;
    border: 2px solid rgb(255, 81, 81);
    background: rgb(255, 63, 63);
    box-shadow: 0 7px 0 0 rgb(246, 22, 22);
    color: white;
    transition: .3s ease-in-out;
    z-index: 1;
}
.reset:hover{
    background: rgb(248, 46, 46);
    letter-spacing: 1px;
}
.reset:focus{
    animation-name: reset-animation;
    animation-duration: .3s;
}

@keyframes reset-animation{
    0% {transform: translateX(20px);}
    25% {transform: translateX(-20px);}
    50% {transform: translateX(10px);}
    75% {transform: translateX(-10px);}
    100% {transform: translateX(0px);}
}

@media (max-width: 576px) { 
    .generate{
        width: 100px;
        font-size: .9rem;
    }
    .reset{
        width: 100px;
        font-size: 1rem;
    }
}